import React from "react";
import { Link } from 'react-router-dom';
function Navbar(props) {
  return (
    <nav className={`navbar navbar-expand-lg sticky-top navbar-${props.mode} bg-${props.mode} px-2`}>
      <div className="container-fluid">
        <Link className="navbar-brand" to="/NewsMonkey/">OnlyNews</Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item"><Link className="nav-link" aria-current="page" to="/NewsMonkey/">Home</Link></li>
            <li className="nav-item"><Link className="nav-link" aria-current="page" to="/NewsMonkey/technology">Technology</Link></li>
            <li className="nav-item"><Link className="nav-link" aria-current="page" to="/NewsMonkey/sports">Sports</Link></li>
            <li className="nav-item"><Link className="nav-link" aria-current="page" to="/NewsMonkey/science">Science</Link></li>
            <li className="nav-item"><Link className="nav-link" aria-current="page" to="/NewsMonkey/health">Health</Link></li>
            <li className="nav-item"><Link className="nav-link" aria-current="page" to="/NewsMonkey/entertainment">Entertainment</Link></li>
            <li className="nav-item"><Link className="nav-link" aria-current="page" to="/NewsMonkey/business">Business</Link></li>
          </ul>
          <div className="form-check form-switch">
            <input className="form-check-input" type="checkbox" role="switch" onClick={props.toggleMode} />
            <label className={`form-check-label text-${props.mode==='light'?'dark':'light'}`} >Enable Dark mode</label>
          </div>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
